import axios from "axios";
import "./HomeModule.css";
import React, { useEffect, useState } from "react";

export default function Home() {
  let [data, setData] = useState();
  const date = new Date();
  let day = [
    "Sunday",
    "Monday",
    "Tuesday",
    "Wednesday",
    "Thursday",
    "Friday",
    "Saturday",
  ];

  async function getWeather(country = "Cairo") {
    let req = await getData();
    setData(req);
  }

  async function ChangeCountry(value) {
    let data = await getData(value).catch((err) => {});
    if (data?.data.forecast) {
      setData(data);
    }
  }

  function getData(country = "Cairo") {
    return axios.get(
      `https://api.weatherapi.com/v1/forecast.json?key=f623af5d7ba94d1a992112233240101&q=${country}&days=3`
    );
  }

  useEffect(() => {
    getWeather();
  }, []);

  return (
    <div>
      <div className="home py-5">
        <div className="container">
          <div class="input-group mb-3 ">
            <input
              type="text"
              class="form-control searchInput"
              placeholder="Recipient's username"
              aria-label="Recipient's username"
              aria-describedby="button-addon2"
              onChange={(eI) => {
                ChangeCountry(eI.target.value);
              }}
            />
            <button
              class="btn btn-outline-secondary btn-search"
              type="button"
              id="button-addon2"
            >
              Search
            </button>
          </div>
          <div className="cards">
            <div className="card" id="card-1">
              <div className="date">
                <span>{day[date.getDay()]}</span>
                <span>{data?.data.forecast.forecastday[0].date}</span>
              </div>
              <div className="info">
                <div className="location">{data?.data.location.name}</div>
                <div className="degree">{data?.data.current.temp_c + "C"}</div>
                <img src={data?.data.current.condition.icon} alt="" />
                <span className="custom">
                  {data?.data.current.condition.text}
                </span>
                <div className="icons">
                  <span>
                    <i class="fa-solid fa-compass"></i>
                    {data?.data.current.wind_dir}
                  </span>
                  <span>
                    <i class="fa-solid fa-wind"></i>
                    {data?.data.current.wind_kph + " Kph"}
                  </span>
                  <span>
                    <i class="fa-solid fa-umbrella"></i>
                    {data?.data.current.humidity}
                  </span>
                </div>
              </div>
            </div>
            <div className="card-2 first">
              <div className="date">
                <span>{day[date.getDay() + 1]}</span>
              </div>
              <img
                src={data?.data.forecast.forecastday[0].day.condition.icon}
                alt=""
              />
              <div className="greatTemp">
                {data?.data.forecast.forecastday[0].day.maxtemp_c + "C"}
              </div>
              <span className="minimumTemp">
                {data?.data.forecast.forecastday[0].day.mintemp_c + "C"}
              </span>
              <div className="custom">
                {data?.data.forecast.forecastday[0].day.condition.text}
              </div>
            </div>
            <div className="card-2 last">
              <div className="date">
                <span>{day[date.getDay() + 2]}</span>
              </div>
              <img
                src={data?.data.forecast.forecastday[1].day.condition.icon}
                alt=""
              />
              <div className="greatTemp">
                {data?.data.forecast.forecastday[1].day.maxtemp_c + "C"}
              </div>
              <span className="minimumTemp">
                {data?.data.forecast.forecastday[1].day.mintemp_c + "C"}
              </span>
              <div className="custom">
                {data?.data.forecast.forecastday[1].day.condition.text}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
